import React, { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import { useGetHostelFeeStructureList } from "../../../../../../hooks/member_tab/hostel-api";
import { useTranslation } from "react-i18next";
import QvipleLoading from "../../../../../../Loader/QvipleLoading";
import ApplicationPreviewDetail from "../../../FinanceManager/FeesStructure/ApplicationPreviewDetail";

const SelectHostelFeeStrucutre = ({
  onClose,
  onSelectedStudent,
  hostelId,
  asOpen,
  batchId,
  classMasterId,
}) => {
  const { t } = useTranslation();
  const [confirm, setConfirm] = useState("");
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [structureList, setStructureList] = useState([]);
  const [feeStructureId, setFeeStructureId] = useState("");
  const { getHostelFeeStructureList, getHostelFeeStructureListRefetch } =
    useGetHostelFeeStructureList({
      data: {
        hid: hostelId,
        page: page,
        limit: 10,
        filter_by: batchId,
        master_by: classMasterId ?? "",
      },
      skip: !hostelId,
    });

  // console.info(hostelId, classMasterId);
  useEffect(() => {
    if (hostelId) {
      setShowingDataLoading(true);
      getHostelFeeStructureListRefetch();
    }
  }, [hostelId, page, getHostelFeeStructureListRefetch]);

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (getHostelFeeStructureList?.all_structures) {
      setStructureList((prevState) =>
        [
          ...new Set(
            [...prevState, ...getHostelFeeStructureList?.all_structures]?.map(
              JSON.stringify
            )
          ),
        ]?.map(JSON.parse)
      );
      setShowingDataLoading(false);
    } else if (getHostelFeeStructureList?.all_structures?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (getHostelFeeStructureList?.all_structures?.length === 10)
      setState(true);
    else setState(false);
  }, [getHostelFeeStructureList?.all_structures]);

  const onSelectedApplicant = (val) => {
    onSelectedStudent({
      fee_struct: feeStructureId?._id,
      ...val,
    });
    onClose();
  };
  const onConfirm = (val) => {
    if (asOpen === "STUDENT_VIEW_STRUCTURE") {
      onSelectedStudent(val?._id);
    } else if (asOpen === "HOSTEL_APPLICATION") {
      onSelectedStudent(val);
    } else {
      setConfirm(val?._id);
      setFeeStructureId(val);
    }
  };
  return (
    <>
      {confirm ? (
        <ApplicationPreviewDetail
          structureId={feeStructureId?._id}
          viewAs="HOSTEL_APPLICATION_TAB"
          setStructureId={setConfirm}
          onSelectedApplicant={onSelectedApplicant}
        />
      ) : (
        <PopupWrapper onClose={onClose}>
          <div
            className={style.add_category_modal}
            style={{
              width: "40rem",
              minHeight: "10rem",
              maxHeight: "35rem",
              overflowY: "auto",
              overflowX: "hidden",
              scrollBehavior: "smooth",
            }}
          >
            <section
              className={style.add_category_modal_title_left}
              style={{ marginBottom: "1.2rem" }}
            >
              <img
                src="/images/arrow-left-fees-icon.svg"
                onClick={onClose}
                alt="back arrow"
              />
              <h6>
                {asOpen === "SELECTED_TAB"
                  ? t("edit_fee_structure")
                  : asOpen === "STUDENT_VIEW_STRUCTURE"
                  ? t("admission_fees")
                  : asOpen === "HOSTEL_APPLICATION"
                  ? t("select_fee_structure_for_application")
                  : t("select_fee_structure_of_student")}
              </h6>
            </section>
            {structureList?.map((category, index) =>
              structureList?.length === index + 1 ? (
                <div
                  className={`${style.fees_card} ${style.fees_card_hover}`}
                  key={category?._id}
                  ref={ref}
                  onClick={() => onConfirm(category)}
                >
                  <img
                    src="/images/fees-category-icon.svg"
                    alt="categor icon"
                  />
                  <div className={style.fees_card_text}>
                    <h6>{category?.unique_structure_name ?? ""}</h6>

                    <div
                      className={style.fees_card_text_bottom}
                      style={{ marginTop: "0.3rem" }}
                    >
                      <h6>
                        {t("applicable_fees_only")} {t("rs")}.
                        {category?.applicable_fees ?? 0}
                      </h6>
                      <h6>
                        {t("total_admission_fees")} {" : "} {t("rs")}.
                        {category?.total_admission_fees ?? 0}
                      </h6>
                    </div>
                  </div>
                </div>
              ) : (
                <div
                  className={`${style.fees_card} ${style.fees_card_hover}`}
                  key={category?._id}
                  onClick={() => onConfirm(category)}
                >
                  <img
                    src="/images/fees-category-icon.svg"
                    alt="categor icon"
                  />
                  <div className={style.fees_card_text}>
                    <h6>{category?.unique_structure_name ?? ""}</h6>

                    <div
                      className={style.fees_card_text_bottom}
                      style={{ marginTop: "0.3rem" }}
                    >
                      <h6>
                        {t("applicable_fees_only")} {t("rs")}.
                        {category?.applicable_fees ?? 0}
                      </h6>
                      <h6>
                        {t("total_admission_fees")} {" : "} {t("rs")}.
                        {category?.total_admission_fees ?? 0}
                      </h6>
                    </div>
                  </div>
                </div>
              )
            )}
            {showingDataLoading && <QvipleLoading />}
          </div>
        </PopupWrapper>
      )}
    </>
  );
};

export default SelectHostelFeeStrucutre;
